* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.module {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 95px;
  margin-top: 96px;
}
.module__image {
  flex-basis: 35%;
}
.module__image img {
  width: 100%;
}
.module__content {
  flex-basis: 26%;
}
.module__title {
  font-size: 42px;
  color: #121212;
  margin-bottom: 24px;
}
.module__text {
  font-weight: 300;
  line-height: 150%;
  margin-bottom: 32px;
}
.module__button {
  height: 48px;
  width: 128px;
  text-align: center;
  border-radius: 32px;
  background: #E4C1AE;
  color: #fff;
  border: none;
  font-weight: 700;
  cursor: pointer;
}